<template>
    <div class="project-opt">
        <div class="step">
            <a-steps :current="step">
                <a-step title="添加项目基本信息"/>
                <a-step title="选择入库项目配置"/>
                <a-step title="选择入库程序配置"/>
            </a-steps>
        </div>
        <div class="stepContent">
            <div class="step0" v-if="step === 0">
                <p class="partTitle">入库项目基本信息</p>
                <a-form-model ref="stepForm0" :model="stepForm0" :rules="stepForm0Rule">
                    <a-row :gutter="[24,24]" type="flex" justify="center">
                        <a-col :xxl="16" :xl="16">
                            <a-form-model-item label="项目名称" prop="name">
                                <a-input v-model="stepForm0.name" placeholder="请输入项目名称"/>
                            </a-form-model-item>
                        </a-col>
                    </a-row>
                    <a-row :gutter="[24,24]" type="flex" justify="center">
                        <a-col :xxl="16" :xl="16">
                            <a-form-model-item label="单位" prop="unitId">
                                <a-select
                                        v-model="stepForm0.unitId"
                                        style="width:calc(100% - 50px)"
                                        placeholder="请选择单位"
                                        allowClear
                                >
                                    <a-select-option
                                            v-for="unit in unitData"
                                            :key="unit.id"
                                            :value="unit.id"
                                    >{{unit.cname}}
                                    </a-select-option>
                                </a-select>
                                <a-tooltip title="添加新单位">
                                    <a-button @click="openUnitModal" ghost class="addIconCls">
                                        <a-icon type="plus"></a-icon>
                                    </a-button>
                                </a-tooltip>
                            </a-form-model-item>
                        </a-col>
                    </a-row>
                    <a-row :gutter="[24,24]" type="flex" justify="center">
                        <a-col :xxl="16" :xl="16">
                            <a-form-model-item label="优先筛选内容" prop="clues">
                                <a-input
                                        v-model="stepForm0.clues"
                                        placeholder="请输入优先筛选内容，多个值以 | 分隔，如：1.1.1.1|admin"
                                />
                            </a-form-model-item>
                        </a-col>
                    </a-row>
                    <a-row :gutter="[24,24]" type="flex" justify="center">
                        <a-col :xxl="16" :xl="16">
                            <a-form-model-item label="项目描述" prop="description">
                                <a-textarea v-model="stepForm0.description" placeholder="请输入项目描述信息"
                                            rows="4"></a-textarea>
                            </a-form-model-item>
                        </a-col>
                    </a-row>
                </a-form-model>
                <div class="stepButtons ar">
                    <a-button type="primary" @click="next(1)">
                        <a-icon type="arrow-right"></a-icon>
                        下一步：入库配置
                    </a-button>
                </div>
            </div>
            <div class="step1" v-if="step === 1">
                <stepConfig></stepConfig>
                <div class="stepButtons ar">
                    <a-button type="primary" @click="next(2)">
                        <a-icon type="arrow-right"></a-icon>
                        下一步：文件配置
                    </a-button>
                    <a-button @click="next(0)">
                        <a-icon type="arrow-left"></a-icon>
                        上一步：添加项目
                    </a-button>
                </div>
            </div>
            <div class="step2" v-if="step === 2">
                <div class="stepButtons ar">
                    <!-- 展示单位下分析对象等详细信息 -->
                    <a-table rowKey="id" :columns="columns" :data-source="tableData">
                        <span slot="action" slot-scope="text,record">
                             <a class="t-optColor-normal" @click="pauseClick(record,1)" :disabled="record.type==='project'||record.type==='object'">跳过</a>
                             <a class="t-optColor-warning" @click="pauseClick(record,2)" :disabled="record.type==='project'||record.type==='object'">恢复</a>
                            <a class="t-optColor-success" :disabled="record.type!=='dirRecords'" @click="fileRecordsDetail(record)">详情</a>
                        </span>
                    </a-table>
                    <a-button @click="start" type="primary">
                        开始入库
                    </a-button>
                    <a-button @click="next(1)">
                        <a-icon type="arrow-left"></a-icon>
                        上一步：入库配置
                    </a-button>
                </div>
            </div>
        </div>
        <!-- 组件 -->
        <unitModal
                :unitVisible="unitVisible"
                @unitOk="unitOk"
                @unitCancel="unitCancel"
                :optType="0"
                :title="'添加单位'"
        ></unitModal>
    </div>
</template>
<script>
    import stepConfig from "./step-config.vue";
    import unitModal from "@/components/myModal/unit-modal.vue";

    export default {
        name: "project-opt",
        components: {stepConfig, unitModal},
        data() {
            return {
                columns: [
                    {
                        dataIndex: 'name',
                        title: '任务名'
                    },
                    {
                        dataIndex: 'statusStr',
                        title: '状态'
                    },
                    {
                        dataIndex: 'fileSize',
                        title: '文件大小'
                    },
                    {
                        dataIndex: 'readSize',
                        title: '已读条数'
                    },
                    {
                        dataIndex: 'dataLength',
                        title: '输出条数'
                    },
                    {
                        dataIndex: 'action',
                        scopedSlots: {customRender: "action"},
                        title: '操作'
                    },
                ],
                tableData: [],
                stepForm0: {
                    name: "",
                    unitId: "",
                    desciption: "",
                    clues: "",
                },
                stepForm0Rule: {
                    name: [{required: true, message: "请输入项目名称", trigger: "blur"}],
                    unitId: [
                        {required: true, message: "请选择单位别名", trigger: "change"},
                    ],
                },
                step: 0,
                // 单位列表
                unitData: [],
                // 单位模态框相关信息
                unitVisible: false,
                projectContent: {},
                projectId:"", // 添加项目生成id
            };
        },
        watch: {
            $route: {
                handler: function () {
                    this.step = parseInt(this.$route.query.step);
                },
                // 深度观察监听
                deep: true,
            },
        },
        methods: {
            openUnitModal() {
                this.unitVisible = true;
            },
            // 添加unit
            unitOk(val) {
                this.$API.addUnit(val.formContent).then((res) => {
                    if (res.code === 1000) {
                        this.$message.success("添加单位成功");
                        this.unitVisible = false;
                        this.getUnit();
                    } else {
                        this.$message.warning(res.message);
                    }
                });
            },
            // 取消unit
            unitCancel() {
                this.unitVisible = false;
            },
            // 根据id查询项目的基本信息
            getProjectContentById(id) {
                return new Promise((resolve) => {
                    this.$API.searchProjectById(id).then((res) => {
                        if (res.code === 1000) {
                            resolve(res.data);
                            this.tableData.push(res.data);
                        } else {
                            resolve(res.data);
                        }
                    });
                });
            },
            // 编辑信息
            editProject(params) {
                return new Promise((resolve) => {
                    this.$API.editProject(params).then((res) => {
                        if (res.code === 1000) {
                            console.log("***********")
                            // this.$message.success("更新项目成功");
                            // this.projectContent = await this.getProjectContentById(pid);
                            resolve(1);
                        } else {
                            this.$message.warning(res.message);
                            resolve(null);
                        }
                    });
                });
            },
            // 添加信息
            addProject(params) {
                return new Promise((resolve) => {
                    this.$API.addProject(params).then((res) => {
                        if (res.code === 1000) {
                            this.projectId = res.data;
                            this.$message.success("创建项目成功");
                            // this.projectContent = await this.getProjectContentById(pid);
                            resolve(res.data);
                        } else {
                            this.$message.warning(res.message);
                            resolve(null);
                        }
                    });
                });
            },
            // 更新step与跳转
            jump(pid, num) {
                // 去编辑数据库中项目的step
                /*if (this.projectContent.step < num) {
                    this.$API
                        .editProject(Object.assign({}, {step: num}, {id: pid}))
                        .then(async (res) => {
                            if (res.code === 1000) {
                                console.log("@@@@@@@@")
                                this.projectContent = await this.getProjectContentById(pid);
                                for (const key in this.stepForm0) {
                                    this.stepForm0[key] = this.projectContent[key];
                                }
                            } else {
                                this.$message.warning(res.message);
                            }
                        });
                }*/
                this.$router.push({
                    name: "project-opt",
                    query: {step: num, previous: this.step, pid: pid},
                });
            },
            async next(num) {
                let flag = true;
                // 每次点击都更新本地的项目的数据
                let pid = this.$route.query.pid;
                // if (pid) {
                //   this.projectContent = await this.getProjectContentById(pid);
                //   for (const key in this.stepForm0) {
                //     this.stepForm0[key] = this.projectContent[key];
                //   }
                // }
                if (num === 1) {
                    if (this.step === 0) {
                        this.$refs.stepForm0.validate(async (valid) => {
                            if (valid) {
                                flag = true;
                                if (pid) {
                                    let data = await this.editProject(
                                        Object.assign({}, this.stepForm0, {
                                            id: this.projectContent.id,
                                        })
                                    );
                                    if (!data) {
                                        flag = false;
                                    }
                                } else {
                                    pid = await this.addProject(Object.assign(this.stepForm0));
                                    if (!pid) {
                                        flag = false;
                                    }
                                }
                            } else {
                                this.$message.warning("输入有误，请检查");
                                flag = false;
                            }
                            this.projectContent = await this.getProjectContentById(pid);
                            this.jump(pid, num);
                        });
                    } else {
                        this.jump(pid, num);
                    }
                } else if (num === 0) {
                    this.jump(pid, num);
                } else {
                    // 点击第二步按钮的操作
                    this.jump(pid, num);
                }
                if (!flag) {
                    return;
                }
            },
            // 获取单位列表
            getUnit() {
                let params = {};
                this.$API.searchUnit(params).then((res) => {
                    if (res.code === 1000) {
                        this.unitData = res.data;
                    } else {
                        this.unitData = [];
                    }
                });
            },
            // 开始
            start() {
                this.$API.editProject(Object.assign({
                    status: 1,
                    id: this.projectContent.id || this.$route.query.pid,
                })).then(res => {
                    if (res.code === 1000) {
                        console.log("^^^^^6")
                        this.projectContent = this.getProjectContentById(this.$route.query.pid);
                        this.$message.success('等待程序开始');
                        this.$router.replace('/business/collect/task');
                    } else {
                        this.$message.success('程序启动错误');
                    }
                })
            },
            // 日志入库-项目列表 详细信息
            searchBulkFileMessage() {
                let params = {};
                this.$API.searchBulkFileMessage(params).then((res) => {
                    if (res.code === 1000) {
                        this.tableData = res.data;
                    } else {
                        this.tableData = [];
                    }
                });
            },
            // 跳过
            pauseClick(column,type){
                if(column.type==="ruleGroup"){
                    // 规则组
                    let params;
                    if(type==="1"){
                        params = {
                            id:column.id,
                            status:"-4"
                        };
                    }else {
                        params = {
                            id:column.id,
                            status:"1"
                        };
                    }
                        this.$API.updateUsedRuleGroup(params).then((res) => {
                            if (res.code === 1000) {
                                this.$message.success("规则组修改成功")
                            }else {
                                this.$message.error("规则组修改跳过")
                            }
                        });

                }else if(column.type==="dirRecords"){
                    // 文件夹
                    let params;
                    if(type==="1"){
                        params = {
                            id:column.id,
                            dirStatus:"-4"
                        };
                    }else {
                        params = {
                            id:column.id,
                            dirStatus:"-4"
                        };
                    }
                    this.$API.updateDirRecords(params).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("文件夹修改成功")
                        }else {
                            this.$message.error("文件夹修改失败")
                        }
                    });
                }else{
                    // 文件
                    let params;
                    if(type==="1"){
                        params = {
                            id:column.id,
                            runStatus:"-4"
                        };
                    }else {
                        params = {
                            id:column.id,
                            runStatus:"-4"
                        };
                    }
                    this.$API.updateFileRecords(params).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("文件修改成功")
                        }else {
                            this.$message.error("文件修改失败")
                        }
                    });
                }
            },
            // 跳转到文件详情
            fileRecordsDetail(record){
                this.$router.push({
                    path:'/business/collect/fileRecordsDetail',
                    query:{
                        id:record.id
                    }
                })
            }
        },
        async created() {
            this.step = parseInt(this.$route.query.step);
            if (this.$route.query.pid) {
                this.projectContent = await this.getProjectContentById(
                    this.$route.query.pid
                );
                for (const key in this.stepForm0) {
                    this.stepForm0[key] = this.projectContent[key];
                }
            }
            this.getUnit();
        },
        mounted() {
            // this.searchBulkFileMessage();
        }
    };
</script>
<style>
    .project-opt
    .ant-steps-item-finish
    > .ant-steps-item-container
    > .ant-steps-item-content
    > .ant-steps-item-title {
        color: #ccc;
    }

    .project-opt
    .ant-steps-item-process
    > .ant-steps-item-container
    > .ant-steps-item-content
    > .ant-steps-item-title {
        color: #5dc2fe;
    }

    .project-opt
    .ant-steps-item-wait
    > .ant-steps-item-container
    > .ant-steps-item-content
    > .ant-steps-item-title {
        color: #ccc;
    }

    .project-opt {
        position: relative;
        margin-top: 40px;
    }

    .project-opt .step {
        position: absolute;
        width: 100%;
        top: -40px;
    }

    .project-opt .stepContent .step0 {
        width: 70%;
        margin: 0 auto;
    }

    .stepButtons {
        margin-top: 20px;
    }

    .stepButtons button {
        margin: 0 10px;
    }

    .addIconCls {
        margin-left: 4px;
    }
    .ant-table-row-collapsed::after{
        color: #1890ff;
    }
    .ant-table-row-expand-icon{
        color: #1890ff;
    }
</style>