<template>
  <div>
    <a-form-model v-model="rule_template" ref="rule_template" :model="rule_template" :rules="ruleFormRule">

      <a-form-model-item label="日志特征">
        <a-input placeholder="用于选择应用本规则的日志 形如login，若无可不填" v-model="rule_template.logFeature" @change="formChange"/>
      </a-form-model-item>
      <a-form-model-item label="日志数据" prop="logData">
        <a-input placeholder="请输入一条日志数据" v-model="rule_template.logData" @change="formChange"/>
      </a-form-model-item>
      <a-form-model-item label="提取表达式">
        <a-input placeholder="用于提取字段，形如 %{IPV4>>ip},%{DATA>>result}" v-model="rule_template.extractRule" @change="formChange"/>
      </a-form-model-item>

      <a-form-model-item label="转换表达式">
        <a-input placeholder="对提取字段进一步处理，形如 ip:$getGeo>>country,city" v-model="rule_template.switchRule" @change="formChange"/>
      </a-form-model-item>

      <a-form-model-item label="替换表达式">
        <a-input placeholder="对提取字段进一步处理，形如 result==succeed>>result=success" v-model="rule_template.replaceRule" @change="formChange"/>
      </a-form-model-item>

      <a-form-model-item label="补充表达式">
        <a-input placeholder="用于补充字段，形如:{“type”:“http”,“opt”:“login”}" v-model="rule_template.supplementRule" @change="formChange"/>
      </a-form-model-item>

    </a-form-model>
  </div>
</template>

<script>

  export default {
    name: "ruleForm",
    components: {},
    props: {
      basic_rules: {
        type: Object,
        default:()=>{}
      },
      proId: {
        type: String
      },
      tabId: {
        type: String
      },
    },
    data() {
      return {
        rule_template: {},
        ruleFormRule: {
              logData: [{ required: true, message: "请输入日志数据",trigger:'blur' }]
          }
      }
    },

    watch: {
     basic_rules: {
        immediate:true,
        handler: function (val) {
          console.log(val)
          this.rule_template = Object.assign({},val);
        },
        deep: true
      }
    },
    methods: {
      formChange(){
        this.$emit('formChange',{data:this.rule_template,proId:this.proId,tabId:this.tabId})
      }
      },
    mounted() {
      // this.switch_props(this.basic_rules)
    }
  }
</script>

