<template>
  <div class="ruleGroup-modal">
    <a-modal
        :title="title"
        :visible="ruleVisible"
        @cancel="handleCancel"
        cancelText="取消"
        okText="确定"
        :mask="false"
        :maskClosable="false"
        :width="600"
        class="modalCls"
    >
      <a-form-model ref="ruleForm" :model="ruleForm" :rules="ruleFormRule">
        <a-row :gutter="[24,24]">
          <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
            <a-form-model-item label="所属规则组" prop="ruleGroupId">
              <a-select v-model="ruleForm.ruleGroupId" placeholder="请选择规则组" allowClear>
                <a-select-option
                    :value="ruleGroup.id"
                    v-for="ruleGroup of ruleGroupData"
                    :key="ruleGroup.id"
                >{{ruleGroup.name}}</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
            <a-form-model-item label="行为类别" prop="category">
              <a-input v-model="ruleForm.category" placeholder="用于标注日志属性" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="规则名称" prop="extractRule">
              <a-input v-model="ruleForm.name" placeholder="请输入规则名称" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="日志特征" prop="extractRule">
              <a-input v-model="ruleForm.logFeature" placeholder="用于选择应用本规则的日志 形如login，若无可不填" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="提取规则表达式" prop="extractRule">
              <a-input v-model="ruleForm.extractRule" placeholder="用于提取字段，形如 %{IPV4>>ip},%{DATA>>result}" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="转换规则表达式" prop="switchRule">
              <a-input v-model="ruleForm.switchRule" placeholder="对提取字段进一步处理，形如 ip:$getGeo>>country,city" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="替换规则表达式" prop="replaceRule">
              <a-input v-model="ruleForm.replaceRule" placeholder="对提取字段进一步处理，形如 result==succeed>>result=success" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="补充规则表达式" prop="supplementRule">
              <a-input v-model="ruleForm.supplementRule" placeholder="用于补充字段，形如:{“type”:“http”,“opt”:“login”}" />
            </a-form-model-item>
          </a-col>
          <a-col :xxl="12" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="规则描述" prop="description">
              <a-textarea v-model="ruleForm.description" placeholder="请输入规则描述" rows="4"></a-textarea>
            </a-form-model-item>
          </a-col>
          <a-col :xxl="24" :xl="24" style="padding: 0 12px 12px;">
            <a-form-model-item label="是否启用" prop="isEnabled">
              <a-switch v-model="ruleForm.isEnabled"></a-switch>
            </a-form-model-item>
          </a-col>
        </a-row>
      </a-form-model>
      <template slot="footer">
        <a-button type="link" ghost @click="handleCancel">取消</a-button>
        <a-button type="primary" @click="handleOk">提交</a-button>
      </template>
    </a-modal>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  export default {
    name: "rule-modal",
    props: {
      // 模态框的显示与隐藏
      ruleVisible: {
        type: Boolean,
        default: false,
        required: true,
      },
      // 模态框的标题
      title: {
        type: String,
        default: "添加/编辑分析对象",
      },
      // 表单内容 一般添加的时候才会传入
      content: {
        type: Object,
        default: function () {
          return {
            name: "",
            code:"",
            objectId:"",
            description: "",
            creator: "",
            gmtCreate: "",
            gmtModified: "",
          };
        },
      },
      // 关于操作类型 0为添加 1为编辑
      optType:{
        type:Number,
        default:0,
        required: true,
      }
    },
    data(){
      return{
        ruleForm: {
          name: "",
          ruleGroupId: "",
          category: "",
          logFeature: "",
          extractRule: "",
          switchRule: "",
          replaceRule: "",
          supplementRule: "",
          description: "",
          isEnabled: true,
        },
        ruleFormRule: {
          name: [{ required: true, message: "请输入规则名称",trigger:'change' }],
          ruleGroupId: [{ required: true, message: "请选择规则组",trigger:'change' }],
          category: [{ required: true, message: "请选择行为类别",trigger:'blur' }],
          // logFeature: [{ required: true, message: "请输入日志特征",trigger:'blur' }],
          extractRule: [{ required: true, message: "请输入提取规则表达式",trigger:'blur' }],
          // switchRule: [{ required: true, message: "请输入转换规则表达式",trigger:'blur' }],
          // replaceRule: [{ required: true, message: "请输入替换规则表达式" ,trigger:'blur'}],
          // supplementRule: [{ required: true, message: "请输入补充规则表达式",trigger:'blur' }],
        },
      }
    },
    computed: {
      ...mapState(["ruleGroupData"]),
    },
    methods:{
      handleOk() {
        console.log(this.ruleForm)
        this.$refs.ruleForm.validate(valid => {
          if(valid){
            this.$emit("ruleOk", {formContent:this.ruleForm,optType:this.optType});
          } else {
            this.$message.warning('输入有误，请检查');
            return false
          }
        })
      },
      handleCancel() {
        this.$emit("ruleCancel", "我点击取消");
      },
    },
    watch:{
      content:{
        handler:function(newVal){
          Object.assign(this.ruleForm,newVal);
          this.ruleForm.isEnabled = !!this.ruleForm.isEnabled;
        },
        deep:true
      },
      ruleVisible:{
        handler:function(newVal){
          if(newVal){
            this.$store.dispatch("getRuleGroupData", this);
          }
        }
      }
    },
    // created(){
    //   this.$API.searchRuleGroup({}).then(res => {
    //     if(res.code === 1000){
    //       this.ruleForm = Object.assign({},res.data,{isEnabled:this.ruleForm.isEnabled?true:false});
    //     }
    //   })
    // }
  }
</script>

<style scoped>

