<template>
    <div class="step-config">
        <div class="step0-info">
            <p class="partTitle">项目基本信息</p>
            <div class="baseContent">
                <a-row :gutter="[24,24]">
                    <a-col :xxl="8" :xl="12" class="clearfix">
                        <div class="label">项目名称：</div>
                        <div class="content">{{projectName}}</div>
                    </a-col>
                    <a-col :xxl="8" :xl="12" class="clearfix">
                        <div class="label">所属单位：</div>
                        <div class="content">{{projectUnitName}}</div>
                    </a-col>
                    <a-col :xxl="24" :xl="24" class="clearfix">
                        <div class="label">项目描述信息：</div>
                        <div class="content">{{projectDescription}}</div>
                    </a-col>
                </a-row>
            </div>

            <p class="partTitle">项目入库配置</p>
            <div class="baseContent">
                <a-row>

                    <a-col span="4">
                        <div>
                            <a-tree
                                    v-model="checkedKeys"
                                    checkable show-line default-expand-all
                                    :expanded-keys="expandedKeys"
                                    :auto-expand-parent="autoExpandParent"
                                    :selected-keys="selectedKeys"
                                    :tree-data="treeData"
                                    @expand="onExpand"
                                    @select="onSelect"
                                    @check="onCheck"
                            >
                                <template slot="custom" slot-scope="item">
                                    <div class="node">
                    <span style="max-width: 25px; overflow: hidden; white-space: nowrap; text-overflow: ellipsis"
                          :title="item.title">{{ item.title }}</span>

                                        <span class="toolbar">

                         <a-tooltip placement="top" v-if="item.level!==3">
                           <template slot="title">
                             <span>添加子节点</span>
                           </template>
                           <a-icon @click="(event)=> onAdd(event, item)" type="plus-circle"></a-icon>
                         </a-tooltip>

                         <a-tooltip placement="top">
                           <template slot="title">
                             <span>修改</span>
                           </template>
                           <a-icon @click="(event)=> onEdit(event, item)" type="edit"></a-icon>
                         </a-tooltip>

                         <a-tooltip placement="top">
                           <template slot="title">
                             <span>删除</span>
                           </template>
                           <a-icon @click="(event)=> onRemove(event, item)" type="delete"></a-icon>
                         </a-tooltip>
                       </span>
                                    </div>
                                </template>
                            </a-tree>
                        </div>
                    </a-col>

                    <a-col offset="5">
                        <a-tabs type="card">
                            <a-tab-pane v-for="(big_pane_value, big_pane_key) in big_tabs"
                                        :tab="big_pane_value.source.objectName"
                                        :key="big_pane_key">
                                <a-form-model>
                                    <a-row v-for="(group_value, group_key) in big_pane_value.rule_groups"
                                           :key="'a_row_'+group_key">

                                        <a-col span="11">
                                            <a-form-model-item :label="group_value.name+'数据文件夹'">
                                                <a-input v-model="group_value.inputPath" placeholder="多个文件夹用分号分隔"/>
                                            </a-form-model-item>
                                        </a-col>

                                        <a-col span="7" offset="1">
                                            <a-form-model-item :label="group_value.name+'文件名正则'">
                                                <a-input v-model="group_value.fileRegex"/>
                                            </a-form-model-item>
                                        </a-col>
                                        <a-col span="4" offset="1">
                                            <a-form-model-item :label="group_value.name+'文件编码'">
                                                <a-select v-model="group_value.fileEncode">
                                                    <a-select-option value="utf-8">utf-8</a-select-option>
                                                    <a-select-option value="gbk">gbk</a-select-option>
                                                    <a-select-option value="utf-16">utf-16</a-select-option>
                                                    <a-select-option value="unicode">unicode</a-select-option>
                                                    <a-select-option value="latin1">latin1</a-select-option>
                                                </a-select>
                                            </a-form-model-item>
                                        </a-col>
                                    </a-row>

                                    <a-tabs hide-add v-model="cur_tab_pane">
                                        <a-tab-pane v-for="(rule_value,rule_key) in big_pane_value.small_tabs"
                                                    :key="rule_key"
                                                    :tab="rule_value.name">
                                            <rule-form :basic_rules="rule_value" :proId="big_pane_key" :tabId="rule_key"
                                                       @formChange="formChange"></rule-form>
                                        </a-tab-pane>
                                    </a-tabs>

                                    <a-form-model-item :wrapper-col="{span:10, offset:14}">
                                        <a-button type="primary" html-type="submit" @click="saveProjectAllRule('save')">
                                            保存当前导入规则
                                        </a-button>
                                        <!-- <a-button style="margin-left: 24px" type="primary" html-type="submit"
                                                  @click="saveProjectAllRule('update')">
                                          保存并更新原始规则
                                        </a-button> -->
                                    </a-form-model-item>
                                </a-form-model>

                            </a-tab-pane>
                        </a-tabs>

                    </a-col>

                </a-row>
            </div>
        </div>
        <object-modal :objectVisible="analysis_object_modal_visible"
                      :title="analysis_object_modal_title"
                      :content="analysis_object" :optType="add_or_modify"
                      @objectOk="objectOk"
                      @objectCancel="objectCancel">

        </object-modal>

        <rule-group-modal :ruleGroupVisible="rule_group_modal_visible"
                          :title="rule_group_modal_title"
                          :content="new_rule_group" :optType="add_or_modify"
                          @ruleGroupOk="ruleGroupOk"
                          @ruleGroupCancel="ruleGroupCancel"
        >

        </rule-group-modal>

        <rule-modal :ruleVisible="rule_modal_visible" :title="rule_modal_title"
                    :content="new_rule" :optType="add_or_modify"
                    @ruleOk="ruleOk" @ruleCancel="ruleCancel">

        </rule-modal>

    </div>
</template>
<script>
    import axios from 'axios'
    import RuleForm from "../../../../components/ruleForm/ruleForm";
    import settingService from "../../../../config/req/api/setting"
    import ObjectModal from "../../../../components/myModal/object-modal";
    import RuleGroupModal from "../../../../components/myModal/ruleGroup-modal";
    import AFormModelItem from "ant-design-vue/es/form-model/FormItem";
    import ACol from "ant-design-vue/es/grid/Col";
    import RuleModal from "../../../../components/myModal/rule-modal";

    // const treeData = [
    //   {
    //     title: "亿邮",
    //     key: "eyou",
    //     level: 1,
    //     scopedSlots: {title: "custom"},
    //     children: [
    //       {
    //         title: "imap",
    //         key: "eyou_imap",
    //         level: 2,
    //         scopedSlots: {title: "custom"},
    //         children: [
    //           {
    //             title: "登陆",
    //             level: 3,
    //             key: "eyou_imap_login",
    //             scopedSlots: {title: "custom"},
    //           },
    //         ]
    //       }
    //     ]
    //   }
    // ];
    export default {
        name: "step-config",
        components: {RuleModal, ACol, AFormModelItem, RuleGroupModal, ObjectModal, RuleForm},
        data() {
            return {
                projectId: '',
                projectName: '',
                projectUnitId: '',
                projectUnitName: '',
                projectDescription: '',
                expandedKeys: [],
                autoExpandParent: true,
                checkedKeys: [],
                big_tabs: {},

                selectedKeys: [],
                treeData: [],
                cur_tab_pane: '',
                keyTreeMap: {},
                cur_node: {},
                add_or_modify: 0,  //0为添加 1为编辑

                new_rule: {},
                new_rule_group: {},
                analysis_object: {},

                rule_modal_visible: false,
                rule_group_modal_visible: false,
                analysis_object_modal_visible: false,

                rule_modal_title: "规则",
                rule_group_modal_title: "规则组",
                analysis_object_modal_title: "分析对象",


                analysis_object_key_map: {},
                rule_group_key_map: {},
            }
        },
        watch: {
            checkedKeys(val, oldVal) {
                let delete_nodes = [];
                val = [...new Set(val)];
                oldVal = [...new Set(oldVal)];
                console.log("--------", val, oldVal);
                // 需要对比出哪些是删除的一级节点
                for (let item of oldVal) {
                    if (val.indexOf(item) < 0) {
                        delete_nodes.push(item)
                    }
                }
                console.log("-----delete_nodes----", delete_nodes);
                delete_nodes.every((item) => {
                    if (this.keyTreeMap[item].level === 3) {
                        let parent_key = this.keyTreeMap[item].parent;
                        let grandpa_key = this.keyTreeMap[parent_key].parent;
                        delete this.big_tabs[grandpa_key].small_tabs[this.keyTreeMap[item].key];
                        if (Object.keys(this.big_tabs[grandpa_key].small_tabs).length === 0) {
                            delete this.big_tabs[this.keyTreeMap[grandpa_key].key];
                        }

                    } else {
                        /*if (this.keyTreeMap[item].level === 1) {
                          delete this.big_tabs[this.keyTreeMap[item].key];
                        }*/
                        if (this.keyTreeMap[item].level === 2) {
                            let grandpa_key = this.keyTreeMap[item].parent;
                            delete this.big_tabs[grandpa_key].rule_groups[this.keyTreeMap[item].key];
                        }
                    }
                    return true
                });

                let selected_node = [].concat(val);
                for (let item of selected_node) {
                    if (this.keyTreeMap[item].level === 3) {
                        let parent_key = this.keyTreeMap[item].parent;
                        let grandpa_key = this.keyTreeMap[parent_key].parent;
                        if (!this.big_tabs[grandpa_key]) {
                            this.big_tabs[grandpa_key] = Object.assign({
                                rule_groups: {},
                                small_tabs: {},
                            }, {source: this.keyTreeMap[grandpa_key].source});
                        }
                        if (!this.big_tabs[grandpa_key]['rule_groups'][parent_key]) {
                            this.big_tabs[grandpa_key]['rule_groups'][parent_key] = this.keyTreeMap[parent_key].source
                        }
                        this.big_tabs[grandpa_key]['small_tabs'][item] = Object.assign({}, this.big_tabs[grandpa_key]['small_tabs'][item], this.keyTreeMap[item].source);
                    }
                }
                console.log("------big_tabs---", this.big_tabs)
            },
        },
        methods: {
            formChange(obj) {
                // console.log(obj)
                // console.log(this.big_tabs)
                this.big_tabs[obj.proId].small_tabs[obj.tabId] = obj.data;
            },
            // opt: save, update
            saveProjectAllRule(opt) {
                let handle_data = Object.assign({}, this.big_tabs);
                console.log("-----handle_data-----", handle_data)
                //一次性拼接，全部project_analysis_mapper, used_rule_group, used_rule的全部数据
                let analysis_object_arr = [];
                let used_rule_group_arr = [];
                let used_rule_arr = [];
                let ruleTest = [];
                for (let [object_key, object_value] of Object.entries(handle_data)) {
                    analysis_object_arr.push({
                        id: object_value.source.projectId ? object_value.source.id : "",
                        projectId: this.projectId,
                        objectId: object_key,
                        objectName: object_value.source.objectName,
                        objectCode: object_value.source.objectCode,
                        status: 0
                    });
                    for (let [rule_group_key, rule_group_value] of Object.entries(object_value.rule_groups)) {
                        if (rule_group_value.inputPath === undefined) {
                            rule_group_value.inputPath = '';
                        }
                        used_rule_group_arr.push({
                            id: rule_group_value.projectAnalysisId ? rule_group_value.id : "",
                            projectAnalysisId: rule_group_value.projectAnalysisId || '',
                            ruleGroupId: rule_group_key,
                            name: rule_group_value.name,
                            code: rule_group_value.code,
                            objectId: rule_group_value.objectId,
                            description: rule_group_value.description,
                            status: 0,
                            inputPath: rule_group_value.inputPath === '' || rule_group_value.inputPath.charAt(rule_group_value.inputPath.length - 1) === '/' ? rule_group_value.inputPath : rule_group_value.inputPath + "/",
                            fileRegex: rule_group_value.fileRegex,
                            fileEncode: rule_group_value.fileEncode,
                        })
                    }
                    for (let [rule_key, rule_item] of Object.entries(object_value.small_tabs)) {
                        used_rule_arr.push({
                            id: rule_item.useRuleGroupId ? rule_item.id : '',
                            useRuleGroupId: rule_item.useRuleGroupId,
                            ruleGroupId: rule_item.ruleGroupId,
                            ruleId: rule_key,
                            name: rule_item.name,
                            description: rule_item.description,
                            category: rule_item.category,
                            logFeature: rule_item.logFeature,
                            logData: rule_item.logData,
                            extractRule: rule_item.extractRule,
                            switchRule: rule_item.switchRule,
                            replaceRule: rule_item.replaceRule,
                            supplementRule: rule_item.supplementRule,
                            isEnabled: 1
                        })
                    }
                    for (let [rule_key_one, rule_item_one] of Object.entries(object_value.small_tabs)) {
                        ruleTest.push({
                            logData: rule_item_one.logData,
                            extractRule: rule_item_one.extractRule,
                            switchRule: rule_item_one.switchRule,
                            replaceRule: rule_item_one.replaceRule,
                            supplementRule: rule_item_one.supplementRule,
                            ruleId: rule_key_one
                        })
                    }
                }
                if (ruleTest[0].extractRule !== "") {
                    if (this.$fun.regularRules(ruleTest[0].extractRule)) {
                        this.$message.warning("提取表达式 >>后面不能是数字，请正确填写");
                        return;
                    }
                }
                let temp;
                let flag = true;
                try {
                    temp = ruleTest[0].supplementRule
                        ? JSON.parse(ruleTest[0].supplementRule)
                        : "";
                    if (
                        temp &&
                        Object.prototype.toString.call(temp) !== "[object Object]"
                    ) {
                        flag = false;
                        this.$message.warning("补充表达式输入格式有误，请正确填写");
                    }
                } catch (err) {
                    flag = false;
                    this.$message.warning("补充表达式输入有误，请正确填写");
                    return;
                }
                if (!flag) {
                    return;
                }
                let ruleTestParams = {
                    src_str: ruleTest[0].logData,
                    extract_rule: ruleTest[0].extractRule,
                    switch_rule: ruleTest[0].switchRule,
                    replace_rule: ruleTest[0].replaceRule,
                    supplement: temp === "" ? {} : temp,
                };
                let params = {
                    objectList: analysis_object_arr,
                    ruleGroupList: used_rule_group_arr,
                    ruleList: used_rule_arr,
                };
                const fun_map = {
                    save: this.$API.updateProjectConfig,
                    update: this.$API.updateProjectConfigAndUpdateRule
                };
                if (ruleTest[0].extractRule !== "") {
                    if (this.$fun.regularRules(ruleTest[0].extractRule)) {
                        this.$message.warning("提取表达式 >>后面不能是数字，请正确填写");
                        return;
                    }
                }
                this.$API.test_rule(Object.assign(ruleTestParams, {
                    testFilePaths: params.ruleGroupList[0].inputPath,
                    testFilrRule: params.ruleGroupList[0].fileRegex,
                    flag: true
                })).then((res) => {
                    if (Array.isArray(res) && res.length > 0) {
                        fun_map[opt](params).then(res => {
                            if (res.code === 1000) {
                                this.$message.success('日志入库项目配置成功');
                            }
                        }).catch((err) => {
                            console.log("-------", err)
                        })
                    } else if (res.error) {
                        this.$message.error(res.error);
                    } else if (res.length === 0) {
                        this.$message.error("出现异常,缺少提取语句或赋值语句");
                    }
                }).catch(err => {
                    console.log(err);
                    this.$message.error("表达式测试服务异常，请联系管理员");
                });
            },
            switchTreeToMap(treeData, parent_key = '') {
                //递归，把树结构数据变成平铺的对象
                for (let item of treeData) {
                    item.parent = parent_key;
                    this.keyTreeMap[item.key] = item;
                    if (item.children && item.children.length) {
                        this.switchTreeToMap(item.children, item.key)
                    }
                }
            },
            objectOk(val) {
                if (val.optType) {
                    let param = Object.assign({}, val.formContent);
                    this.$API.editObject(param).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("分析对象更新成功");
                            this.analysis_object_modal_visible = false;
                            this.searchAllData();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                } else {
                    this.$API.addObject(val.formContent).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("添加分析对象成功");
                            this.analysis_object_modal_visible = false;
                            this.searchAllData();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                }
            },
            objectCancel() {
                this.analysis_object_modal_visible = false;
                // this.resetContent();
            },
            ruleGroupOk(val) {
                if (val.optType) {
                    let param = Object.assign({}, val.formContent);
                    this.$API.editRuleGroup(param).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("规则组更新成功");
                            this.rule_group_modal_visible = false;
                            this.searchAllData();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                } else {
                    this.$API.addRuleGroup(val.formContent).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("添加规则组成功");
                            this.rule_group_modal_visible = false;
                            this.searchAllData();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                }
            },
            ruleGroupCancel() {
                this.rule_group_modal_visible = false;
                // this.resetContent();
            },

            ruleOk(val) {
                let params = {
                    name: val.formContent.name,
                    id: val.formContent.id,
                    category: val.formContent.category,
                    creator: val.formContent.creator,
                    isEnabled: val.formContent.isEnabled ? 1 : 0,
                    ruleGroupId: val.formContent.ruleGroupId,
                    extractRule: val.formContent.extractRule,
                    replaceRule: val.formContent.replaceRule,
                    supplementRule: val.formContent.supplementRule,
                    switchRule: val.formContent.switchRule,
                };
                if (val.optType) {
                    this.$API.editRule(params).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("编辑规则成功");
                            this.rule_modal_visible = false;
                            this.searchAllData();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                } else {
                    this.$API.addRule(params).then((res) => {
                        if (res.code === 1000) {
                            this.$message.success("添加规则成功");
                            this.rule_modal_visible = false;
                            this.searchAllData();
                        } else {
                            this.$message.warning(res.message);
                        }
                    });
                }
                this.new_rule = {};
                this.rule_modal_visible = false;
            },
            ruleCancel() {
                this.new_rule = {};
                this.rule_modal_visible = false;
            },
            onCheck(checkedKeys, event) {
                this.cur_tab_pane = event.node.eventKey;
            },
            onExpand(expandedKeys) {
                console.log('onExpand', expandedKeys);
                // if not set autoExpandParent to false, if children expanded, parent can not collapse.
                // or, you can remove all expanded children keys.
                this.expandedKeys = expandedKeys;
                this.autoExpandParent = false;
            },
            onSelect(selectedKeys, info) {
                console.log('onSelect', info);
                if (info.node.pos.split('-').length === 4) {
                    this.cur_tab_pane = info.node.eventKey
                }
                this.selectedKeys = selectedKeys;
            },
            onAdd(event, item) {
                this.add_or_modify = 0;
                event.stopPropagation();
                let level = item.pos.split('-').length;
                if (level === 2) {
                    this.rule_group_modal_visible = true;
                } else {
                    this.rule_modal_visible = true;
                }
            },
            onEdit(event, item) {
                this.add_or_modify = 1;
                console.log("-----edit----", item)
                event.stopPropagation();
                let level = item.pos.split('-').length;
                if (level === 2) {
                    this.analysis_object_modal_visible = true;
                    this.analysis_object = item.source;
                } else {
                    if (level === 3) {
                        this.rule_group_modal_visible = true;
                        this.new_rule_group = item.source
                    } else {
                        this.rule_modal_visible = true;
                        this.new_rule = item.source
                    }
                }
            },
            onRemove(event, item) {
                event.stopPropagation();
                if (item.children && item.children.length > 0) {
                    return this.$confirm({
                        title: '提示',
                        content: '当前节点有子节点， 不可直接删除！',
                        okText: '确认',
                        cancelText: '取消'
                    })
                }
                let _this = this;
                let fun_map = {
                    2: this.$API.delObject,
                    3: this.$API.delRuleGroup,
                    4: this.$API.delRule
                }
                this.$confirm({
                    title: '警告',
                    content: "确认删除此节点？",
                    okText: '确认',
                    cancelText: '取消',
                    onOk() {
                        let level = item.pos.split('-').length;
                        fun_map[level](item.source.id).then(res => {
                            console.log("---res----", res)
                            if (res.code === 1000) {
                                _this.$message.success('删除成功');
                                _this.searchOption(item, _this.treeData);
                            }
                        })
                    },
                    onCancel() {
                    }
                })
            },
            searchOption(option, arr, type = "delete") {
                console.log(option, arr);
                for (let s = 0; s < arr.length; s++) {
                    console.log(arr[s].key, option.key);
                    if (arr[s].key === option.key) {
                        if (type === "delete") {
                            arr.splice(s, 1);
                        } else {
                            //这是模拟数据编辑数据
                            this.$set(arr, s, {
                                title: "12121212",
                                key: "12121212",
                                scopedSlots: {title: "custom"}
                            });
                        }
                        break;
                    } else if (arr[s].children && arr[s].children.length > 0) {
                        // 递归条件
                        this.searchOption(option, arr[s].children);
                    } else {
                        continue;
                    }
                }
            },
            // 查询出一棵树
            searchAllData() {
                axios.all([
                    settingService.searchObject({}),
                    settingService.searchRuleGroup({}),
                    settingService.searchRule({})
                ]).then(axios.spread((analysis_objects, rule_groups, rules) => {
                    let rule_map_by_group = {};
                    rules.data.every((item) => {
                        if (!rule_map_by_group[item.ruleGroupId]) {
                            rule_map_by_group[item.ruleGroupId] = [];
                        }
                        if (item.ruleGroupId) {
                            rule_map_by_group[item.ruleGroupId].push({
                                key: item.id,
                                level: 3,
                                title: item.name,
                                scopedSlots: {title: "custom"},
                                source: item
                            });
                        }
                        return true;
                    });

                    let rule_group_map_by_object = {};
                    rule_groups.data.every((item) => {
                        this.rule_group_key_map[item.id] = item.name;
                        if (!rule_group_map_by_object[item.objectId]) {
                            rule_group_map_by_object[item.objectId] = [];
                        }
                        if (item.objectId) {
                            rule_group_map_by_object[item.objectId].push({
                                key: item.id,
                                level: 2,
                                title: item.name,
                                scopedSlots: {title: "custom"},
                                source: item,
                                children: rule_map_by_group[item.id] || []
                            });
                        }
                        return true;
                    });

                    let tree_data = [];
                    analysis_objects.data.every((item) => {
                        this.analysis_object_key_map[item.id] = item.objectName;
                        let object_node = {
                            key: item.id,
                            level: 1,
                            title: item.objectName,
                            scopedSlots: {title: "custom"},
                            source: item,
                            children: rule_group_map_by_object[item.id] || []
                        };
                        tree_data.push(object_node);
                        return true;
                    });
                    this.treeData = tree_data;
                    this.switchTreeToMap(tree_data)
                }))
            },
            searchProjectConfig() {
                this.$API.searchProjectConfig(this.projectId).then(res => {
                    console.log("-----res--", res);
                    //循环拼接成config实用的对象
                    if (res.code === 1000) {
                        let use_rule_group_map_by_object = {};
                        let use_rule_map_by_object = {};
                        let use_rg_object_map = {};

                        res.data[0].useRuleGroupList.every((item) => {
                            if (!use_rule_group_map_by_object[item.objectId]) {
                                use_rule_group_map_by_object[item.objectId] = {}
                            }
                            use_rg_object_map[item.ruleGroupId] = item.objectId;
                            use_rule_group_map_by_object[item.objectId][item.ruleGroupId] = item;
                            return true;
                        });

                        res.data[0].useRuleList.every((item) => {
                            let _rule_group_id = item.ruleGroupId;
                            let _object_id = use_rg_object_map[_rule_group_id];
                            if (!use_rule_map_by_object[_object_id]) {
                                use_rule_map_by_object[_object_id] = {}
                            }
                            use_rule_map_by_object[_object_id][item.ruleId] = item;
                            return true;
                        })

                        let data = {};
                        res.data[0].objectList.every((item) => {
                            data[item.objectId] = {};
                            data[item.objectId].source = item;
                            data[item.objectId].rule_groups = use_rule_group_map_by_object[item.objectId];
                            data[item.objectId].small_tabs = use_rule_map_by_object[item.objectId];
                            return true;
                        });
                        this.big_tabs = data;
                    }
                })
            },
            searchProjectInfo() {
                this.$API.searchProjectById(this.projectId).then(res => {
                    this.projectName = res.data.name;
                    this.projectDescription = res.data.description;
                    this.projectUnitId = res.data.unitId;
                    this.projectUnitName = res.data.unitName;
                }).catch((err) => {
                    console.log("---------err:", err)
                })
            }
        },
        mounted() {
            console.log("---------query----", this.$route.query);
            this.searchAllData();
            // 进入此页面，肯定有项目id，与获取该项目的入库配置
            let project_id = this.$route.query.pid;
            if (!project_id) {
                this.$message.error('当前无项目')
            } else {
                this.projectId = project_id;
                this.searchProjectConfig();
                this.searchProjectInfo();
            }

        }
    };
</script>
